<template>
	<f7-page>
		<f7-navbar title="CP002 Wallet β" link="/">
			<f7-nav-right v-if="isLogin">
				<f7-link icon="icon-bars" panel-open="right">
					<f7-icon material="menu"></f7-icon>
				</f7-link>
			</f7-nav-right>
		</f7-navbar>
		<div v-if="isLogin">
			<div v-if="balances">
				<f7-list>
					<f7-list-item link="/balance" title="Ваш общий баланс" footer="Сумма доступных средств" :after="balanceSum + ' BIP'"></f7-list-item>
					<f7-list-item link="/balance" title="Из них в кастомах" footer="Эквивалент в bip" :after="balanceCustom + ' BIP'"></f7-list-item>
					<f7-list-item v-if="delegations && delegations.length" link="/delegations" title="Делегированно" footer="Эквивалент в bip" :after="delegationsSum + ' BIP'"></f7-list-item>
				</f7-list>
			</div>
			<f7-block class="text-align-center" v-else>
				<f7-preloader :size="60"></f7-preloader>
			</f7-block>

			<f7-block v-if="payments && payments.length">
				<f7-block-title>Запросы на перевод</f7-block-title>
				<payment v-for="(item, index) in payments" :item="item" :index="index"></payment>
			</f7-block>
			<f7-block v-else>
				<f7-block-title>Запросов нет</f7-block-title>
			</f7-block>
			<f7-block>
				<f7-block-title>Счетов нет</f7-block-title>
			</f7-block>
			<!-- <f7-block>
				<f7-button raised outline round @click="testSend">Demo: Отправить себе 0.1 bip</f7-button>
			</f7-block> -->
		</div>
		<f7-block v-else>
			<p>Для начала нужно авторизоваться</p>
			<f7-button raised large fill href="/login">Войти</f7-button>
		</f7-block>

	</f7-page>
</template>

<script>
import {mapGetters, mapState} from 'vuex';
import cpAgent from '../utils/cp002-agent'
import Payment from '@/components/Payment'

export default {
	name: "Home",
	components: {
		Payment
	},
	data() {
		return {
		}
	},
	methods: {
		testSend() {
			const txParams = {
				txAction: "SendTxParams",
				address: this.address,
				amount: 0.1,
				coinSymbol: 'BIP',
				feeCoinSymbol: 'BIP',
				message: "Test cp002",
			};
			cpAgent("payment", this.address, txParams).then(data=>{
				console.log("service ", data)
			});
		},
		wsopen(e){
			this.wsPayment.send(JSON.stringify({
				from: "user",
				status: "open",
				address: this.address
			}));
		},
		wsclose(e){
			console.log("close", e);
		},
		wsmessage(event){
			let msg = false;
			try {
				msg = JSON.parse(event.data);
			} catch(e) {
				// this.wsPayment.send(errors["400"]);
			}
			console.log("message", msg);
			if((msg.from == "user" && msg.status == "pending") || (msg.from == "service" && msg.action == "payment")){
				this.$store.dispatch('SET_PAYMENTS', msg);
			}
		}
	},
	computed: {
		...mapState({
			address: state => state.wallet.address,
			balances: state => state.api.balances,
			delegations: state => state.api.delegations,
			wsPayment:  state => state.payments.wsPayment,
			payments:  state => state.payments.payment,
			balanceList: state => state.api.balanceList,
		}),
		...mapGetters([
			'isLogin',
			'balanceSum',
			'balanceCustom',
			'delegationsSum',
		]),
	},
	created() {
		if(this.isLogin){
			// this.$store.dispatch('FETCH_BALANCE');
			// this.$store.dispatch('FETCH_DELEGATION');
			
			this.$store.dispatch('WS_PAYMENT');
			this.wsPayment.onopen = (e) => this.wsopen(e);
			this.wsPayment.onmessage = (e) => this.wsmessage(e);
			this.wsPayment.onclose = (e) => this.wsclose(e);
		}
	},
	mounted() {
		if(this.isLogin){
		}
	}
}
</script>