<template>
	<f7-page
		infinite
		:infinite-distance="50"
		:infinite-preloader="showPreloader"
		@infinite="loadMore">
		<f7-navbar title="История" back-link="Назад"></f7-navbar>
		<f7-block>
			<p><b>Страница транзакций в разработке</b></p>
		</f7-block>
		<f7-list>
			<f7-list-item v-for="(item, index) in transactions" :key="index">
				<f7-button fill sheet-open=".demo-sheet-swipe-to-close">{{ index }}{{ item.txn }}</f7-button>
			</f7-list-item>
		</f7-list>

		<f7-sheet
			class="demo-sheet-swipe-to-close"
			style="height:auto; --f7-sheet-bg-color: #fff;"
			swipe-to-close
			backdrop
			>
			<f7-page-content>
				<f7-block-title large>В разарботке</f7-block-title>
				<f7-block>
					<p>Информация о транзакциях появиться при следующих обновлениях</p>
				</f7-block>
			</f7-page-content>
		</f7-sheet>
	</f7-page>
</template>


<script>
import {mapGetters, mapState} from 'vuex';
import axios from 'axios';

export default {
	name: "Home",
	components: {},
	data() {
		return {
			transactions: [],
			meta: null,
			allowInfinite: true,
			showPreloader: true,
			loadUrl: "",
		};
	},
	methods: {
		loadMore(){this;
			if (!this.allowInfinite) return;
			this.allowInfinite = false;

			axios.get(this.loadUrl)
				.then((res) => {
					this.meta = res.data.meta;
					this.transactions = this.transactions.concat(res.data.data);
					if(res.data.data.length == 50 && res.data.links.next != null) {
						this.showPreloader = true;
						this.allowInfinite = true;
						this.loadUrl = "https://" + res.data.links.next;
					}else{
						this.showPreloader = false;
						this.allowInfinite = false;

					}
				}).catch(() => {});

			// setTimeout(() => {
			// 	if (self.items.length >= 200) {
			// 		self.showPreloader = false;
			// 		return;
			// 	}

			// 	const itemsLength = self.items.length;

			// 	for (let i = 1; i <= 20; i += 1) {
			// 		self.items.push(itemsLength + i);
			// 	}

			// 	self.allowInfinite = true;
			// }, 1000);
		}
	},
	computed: {
		...mapState({
			explorerApi: state => state.api.explorerApi,
			address: state => state.wallet.address,
			// transactions: state => state.api.transactions,
		}),
		...mapGetters([
			'isLogin',
		]),
	},
	created() {
		this.loadUrl = this.explorerApi + "addresses/"+ this.address + "/transactions";
		this.loadMore();
	},
	mounted() {
	}
}
</script>