<template>
	<f7-page>
		<f7-navbar title="Делегированно" back-link="Назад"></f7-navbar>
		<f7-list v-if="delegations.length">
			<f7-list-item v-for="item in delegations" :footer="item.validator_meta.name" :title="item.coin" :after="numberSpaces(pretty(item.value, 5))">
				<img width="34" slot="media" :src="item.validator_meta.icon_url">
				<!-- <img width="34" slot="media" :src="'https://my.minter.network/api/v1/avatar/by/coin/' + item.coin"> -->
			</f7-list-item>
		</f7-list>
		<f7-block v-else>
			<p>У вас ничего не делегированно. Может стоит попробовать?</p>
		</f7-block>
	</f7-page>
</template>

<script>
import { mapGetters, mapState } from 'vuex';
import { pretty, numberSpaces } from '../utils';

export default {
	name: "Delegation",
	data() {
		return {
		}
	},
	methods: {
		pretty(val, l) { return pretty(val, l) },
		numberSpaces(val) { return numberSpaces(val) }
	},
	computed: {
		...mapState({
			address: state => state.wallet.address,
			delegations: state => state.api.delegations,
		}),
		...mapGetters([
			'isLogin',
		]),
	},
	created() {},
	mounted() {
	}
}
</script>