// import axios from 'axios';
import { Minter, SendTxParams, DelegateTxParams, API_TYPE_NODE } from "minter-js-sdk";

const state = {
	address: null,
	public: null,
	private: null,
	minterGate: null,
};

const getters = {
	isLogin: state => (!!state.address && !!state.address.length),
};

const mutations = {
	SAVE_WALLET: (state, payload) => {
		state.address = payload.address;
		state.public = payload.public;
		state.private = payload.private;
	},
	SAVE_GATE: (state, payload) => {
		state.minterGate = new Minter({apiType: API_TYPE_NODE, baseURL: 'https://api.minter.stakeholder.space/'});
	},
	LOGOUT: async (context, payload) => {
		localStorage.removeItem('wallet');
		state.address = null;
		state.public = null;
		state.private = null;
		state.minterGate = null;
	},
};

const actions = {
	SET_WALLET: (context, payload) => {
		localStorage.setItem('wallet', JSON.stringify(payload));
		context.commit('SAVE_WALLET', payload);
	},
	SET_GATE: (context, payload) => {
		context.commit('SAVE_GATE', payload);
	},
	SENDER: (context, payload) => {
		payload.privateKey = state.private;
		payload.chainId = 1;
		let txParams = null;
		switch (payload.txAction){
			case "SendTxParams":
				txParams = new SendTxParams(payload);
				break;
			case "DelegateTxParams":
				txParams = new DelegateTxParams(payload);
				break;
			default:
				return false;
				break;
		};
		return new Promise((resolve, reject)=> {
			state.minterGate.postTx(txParams).then((txHash) => {
				// console.log(txData.txAction + " created: " + txHash);
				resolve(txHash)
			}).catch((error) => {
				reject("false");
			});
		});
	}
};

export default {
	state,
	getters,
	mutations,
	actions,
};