<template>
	<f7-page>
		<f7-navbar title="Отправка" back-link="Назад"></f7-navbar>
		
		<f7-list form no-hairlines-md >
			<f7-list-input
				type="text"
				floating-label
				required
				outline
				label="Адрес"
				:value="addressTo"
				@input="addressTo = $event.target.value"
				info="Поддерживаются Mx и Mp адреса"
				clear-button>
			</f7-list-input>
			<f7-list-input
				outline
				label="Монета"
				floating-label
				:value="coinSymbol"
				@input="coinSymbol = $event.target.value"
				type="select">
				<option v-for="(amount, coin) in balancesList" :value="coin">{{ coin }} - {{ amount }}</option>
			</f7-list-input>
			<f7-list-input
				type="number"
				outline
				floating-label
				step="any"
				label="Количество"
				:value="quantity"
				@input="quantity = $event.target.value"
				clear-button>
				<f7-button outline slot="media" @click.prevent="maxValue">max</f7-button>
			</f7-list-input>
			 <f7-list-input
				outline
				:value="message"
				@input="message = $event.target.value"
				label="Сообщение"
				floating-label
				type="textarea"
				clear-button>
			</f7-list-input>
			<f7-list-item>
				Комиссия сети: {{ senderFee }} bip
			</f7-list-item>
			<f7-list-item>
				<f7-button @click.prevent="sender" :disabled="!validateForm" fill type="submit">Отправить</f7-button>
			</f7-list-item>
		</f7-list>
		<f7-block v-if="totalString.length">
			<p>{{ totalString }}</p>
			<div class="sender__profile" v-if="txAction=='SendTxParams' && profileTo">
				<div class="sender__profile-title">Получатель:</div>
				<img class="sender__profile-img" v-if="profileTo.icons.webp" :src="profileTo.icons.webp" width="60">
				<div class="sender__profile-data">
					<b>{{ profileTo.title }}</b><br>
					{{ profileTo.www }}
				</div>
			</div>
			<div class="sender__profile" v-if="txAction=='DelegateTxParams' && profileTo">
				<div class="sender__profile-title">Валидатор:</div>
				<img class="sender__profile-img" v-if="profileTo.meta.icon_url" :src="profileTo.meta.icon_url" width="60">
				<div class="sender__profile-data">
					<b>{{ profileTo.meta.name }}</b><br>
					{{ profileTo.meta.site_url }}
				</div>
			</div>
			<div class="sender__profile" v-if="profileTo == null">
				<div class="sender__profile-title">Получатель:</div>
				<b>{{ addressTo }}</b>
			</div>
		</f7-block>
	</f7-page>
</template>

<style lang="stylus">
	.sender {
		&__profile {
			display: flex;
			word-break: break-word;
			flex-wrap: wrap;
			&-title {
				flex: 0 0 100%;
				padding-bottom: 10px;
			}
			&-img {
				flex: 0 0 60px;
				margin-right: 15px;
			}
			&-data {
				flex: 1 0 0;
			}
		}
	}
</style>
<script>
// import { SendTxParams, DelegateTxParams } from "minter-js-sdk";
import { getFeeValue } from 'minterjs-util';
import { TX_TYPE_SEND } from 'minterjs-tx';
import { mapGetters, mapState } from 'vuex';
import { pretty, numberSpaces, notify } from '../utils';

export default {
	name: "Send",
	data() {
		return {
			coinSymbol: "",
			addressTo: "",
			quantity: "",
			message: "",
			addressToOld: "",
			validateForm: false,
			txAction: "",
			totalString: "",
			loadedProfile: false,
			profileTo: null,
			senderFee: 0.01
		}
	},
	methods: {
		validateFormFn() {
			this.validateForm = true;
			if(!this.coinSymbol.length) this.validateForm = false;
			if(this.addressTo.length){
				if(this.addressTo != this.addressToOld){
					this.addressToOld = this.addressTo;
					if(this.addressTo.substring(0,2) == "Mx" && this.addressTo.length == 42){
						this.txAction = "SendTxParams";
						this.$store.dispatch('GET_PROFILE', this.addressTo).then((data)=>{
							this.profileTo = data;
						}).catch(()=>{
							this.profileTo = null;
						});
					}else if(this.addressTo.substring(0,2) == "Mp" && this.addressTo.length == 66){
						this.txAction = "DelegateTxParams";
						this.$store.dispatch('GET_VALIDATOR', this.addressTo).then((data)=>{
							this.profileTo = data;
						}).catch(()=>{
							this.profileTo = null;
						});
					} else this.validateForm = false;
				}
			} else { this.validateForm = false; }
			if(this.quantity.toString().length){
				if(!(parseFloat(this.quantity) <= parseFloat(this.balancesList[this.coinSymbol]))) this.validateForm = false;
			} else this.validateForm = false;
			if(this.validateForm) {
				this.totalString = `Вы собираетесь ${this.txAction == "SendTxParams" ? "отправить" : "делегировать"} ${this.quantity} ${this.coinSymbol}`;
			}else{
				this.totalString = "";
			}
			
		},
		sender(){
			if(this.validateForm){
				let txData = {
					txAction: this.txAction,
					coinSymbol: this.coinSymbol,
					feeCoinSymbol: 'BIP',
					message: this.message,
				};
				if(this.txAction == "SendTxParams"){
					txData.amount = this.quantity; 
					txData.address = this.addressTo;
				}else if(this.txAction == "DelegateTxParams"){
					txData.stake = this.quantity;
					txData.publicKey = this.addressTo;
				}
				this.$store.dispatch('SENDER', txData).then((txHash) => {
					console.log(txHash);
					this.clearAll();
					this.$f7.dialog.alert('Транзакция совершена успешно');
					// notify(this.$f7, "Транзакция совершена", "");
				}).catch(()=>{
					// notify(this.$f7, "Ошибка транзакции", "");
					this.$f7.dialog.alert('Ошибка в транзакции');
				});
			}
		},
		clearAll(){
			this.coinSymbol = "";
			this.addressTo = "";
			this.quantity = "";
			this.message = "";
			this.addressToOld = "";
			this.validateForm = false;
			this.txAction = "";
			this.totalString = "";
			this.loadedProfile = false;
			this.profileTo = null;
			this.senderFee = 0.01;
		},
		pretty(val, l) { return pretty(val, l) },
		numberSpaces(val) { return numberSpaces(val) },
		maxValue() { this.quantity = parseFloat(this.balancesList[this.coinSymbol]); }
	},
	computed: {
		...mapState({
			address: state => state.wallet.address,
			private: state => state.wallet.private,
			balances: state => state.api.balances,
			balancesList: state => state.api.balancesList,
		}),
		...mapGetters([
			'isLogin',
		]),
	},
	created() {
	},
	mounted() {
		if(this.balances && this.balances.balances.length) {
			this.coinSymbol = this.balances.balances[0].coin;
		}else{
			let self = this;
			setTimeout(()=>{
				self.coinSymbol = self.balances.balances[0].coin;
			},1000);
		}
	},
	watch: {
		// balancesList(){ console.log(this.balancesList) },
		coinSymbol(){ this.validateFormFn() },
		addressTo(){ this.validateFormFn() },
		quantity(newV,oldV){ this.validateFormFn() },
		message(payload){ this.senderFee = getFeeValue(TX_TYPE_SEND, {payload: payload}); }
	}
}
</script>