<template>
	<f7-page>
		<f7-navbar title="Мой адрес" back-link="Назад"></f7-navbar>
		<f7-block>
			<b>{{ address }}</b>
		</f7-block>
		<f7-block>
			<f7-button raised outline @click="copyAddress">Скопировать адрес</f7-button>
		</f7-block>
		<f7-block v-if="qrimg && qrimg.length" class="address__qr">
			<img :src="qrimg">
		</f7-block>
	</f7-page>
</template>


<script>
import {mapState} from 'vuex';
import QRCode from 'qrcode'

export default {
	name: "Home",
	components: {},
	data() {
		return {
			qrimg: ""
		}
	},
	methods: {
		copyAddress(){
			navigator.clipboard.writeText(this.address)
				.then(() => {
					this.notify()
					// console.log("address copy ", text)
				})
				.catch(() => {})
		},
		notify(){
			var notificationClickToClose = this.$f7.notification.create({
				icon: '<i class="icon material-icons">info</i>',
				title: 'От CP002 wallet',
				titleRightText: '',
				subtitle: 'Адрес скопирован',
				// text: 'Нажми чтоб закрыть',
				closeOnClick: true,
			});
			notificationClickToClose.open();
			setTimeout(()=>{
				notificationClickToClose.close();
			}, 2000);
		},
		createQR(){
			var opts = {
				errorCorrectionLevel: 'H',
				type: 'image/png',
				rendererOpts: {
					quality: 10
				}
			};
			QRCode.toDataURL(this.address, opts)
				.then(url => {
					console.log(url)
					this.qrimg = url;
				})
				.catch(err => {
					console.error(err)
				})
		}
	},
	computed: {
		...mapState({
			address: state => state.wallet.address,
		}),
	},
	created() {
		this.createQR();
	},
	mounted() {
	}
}
</script>
<style type="text/css">
	.address__qr {
		text-align: center;
	}
</style>