<template>
	<f7-card>
		<f7-card-header class="no-border" valign="bottom">
			<div>
				От: <b>{{ item.requesting }}</b>
			</div>
		</f7-card-header>
		<f7-card-content v-if="item.status=='pending'" v-html="humanFriendly(item.txArray[0])"></f7-card-content>
		<div v-if="item.txArray.length==2">
			<f7-block>Затем</f7-block>
			<f7-card-content v-html="humanFriendly(item.txArray[0])"></f7-card-content>
		</div>
		
		<f7-card-content v-if="item.status=='reject'">
			<f7-icon material="block"></f7-icon>&nbsp;
			Вы отменили запрос
		</f7-card-content>
		<f7-card-content v-if="item.status=='resolve'">
			<f7-icon material="swap_horiz"></f7-icon>&nbsp;
			Вы совершили транзакцию
		</f7-card-content>
		<f7-card-content v-if="item.status=='timeout'">
			<f7-icon material="av_timer"></f7-icon>&nbsp;
			Время транзакции истекло
		</f7-card-content>

		<f7-block v-if="item.status=='pending'">
			<f7-row>
				<f7-col>
					<f7-button @click="reject" outline color="red">Отказать</f7-button>
				</f7-col>
				<f7-col>
					<f7-button @click="resolve" outline >Подписать</f7-button>
				</f7-col>
			</f7-row>
		<br>
		</f7-block>
		<f7-card-footer v-if="item.status=='pending'">
			<f7-preloader color="orange"></f7-preloader>
			Запрос активен еще 
			{{ timerSeconds }} секунд
		</f7-card-footer>
	</f7-card>
</template>

<style type="text/css">
	.card-content {
		word-break: break-word;
	}
</style>

<script>

import { mapState } from 'vuex';
import { Minter, SendTxParams, API_TYPE_NODE } from "minter-js-sdk";
import { pretty, notify } from '../utils';

export default {
	name: 'RequestItem',
	props: ['item', 'index'],
	data() {
		return {
			timerSeconds: 0,
			loaded: false,
			intervalID: null,
		}
	},
	methods: {
		humanFriendly(tx){
			let text = "Сервис просит ";
			switch (tx.txAction){
				case "SendTxParams":
					text += "отправить "
					text += "<b>" + pretty(tx.amount, 5);
					text += " " + tx.coinSymbol + "</b>";
					text += " на адрес <br><b>" + tx.address + "</b>";
					if(tx.message && tx.message.length){
						text += "<p>С сообщением:<br> " + tx.message + "</p>";
					}
					break;
			}
			return text;
		},
		
		countdown(){
			let now = new Date(this.item.date);
			let created = new Date(this.item.created);
			let dif = now.getTime() - created.getTime();
			let duration = Math.abs(60 - dif / 1000);
			let timer = duration, seconds;
			this.intervalID = setInterval(() => {
				seconds = parseInt(timer % 60, 10);
				seconds = seconds < 10 ? "0" + seconds : seconds;
				this.timerSeconds = seconds;

				if (--timer < 0) {
					clearInterval(this.intervalID);
					this.$store.commit('CHANGE_STATUS', {index: this.index, type: "payment", status: "timeout"});
				}
			}, 1000);
		},
		delay(ms) {
			return new Promise((resolve, reject) => {
				setTimeout(resolve, ms);
			});
		},
		resolve(){
			let requestResult = [];
			if(this.item.txArray.length==2){
				this.$f7.dialog.preloader();
			}

			this.$store.dispatch('SENDER', this.item.txArray[0])
				.then((tx1Hash) => {
					requestResult.push(tx1Hash);
					if(this.item.txArray.length==1){
						return new Promise(resolve => resolve());
					}else if(this.item.txArray.length==2){
						return this.delay(6500).then(()=> {
							return this.sendTransaction(this.item.txArray[1])
						});
					}
				}).then((tx2Hash) => {
					if(this.item.txArray.length==2 && !!tx2Hash) {
						requestResult.push(tx2Hash);
					}
					if(this.item.txArray.length==2){
						this.$f7.dialog.close();
					}
					notify(this.$f7, "Транзакция совершена", this.item.requesting);
					clearInterval(this.intervalID);
					this.wsPayment.send(JSON.stringify({
						from: "user",
						status: "resolve",
						_id: this.item._id,
						hash: requestResult
					}));
					this.$store.commit('CHANGE_STATUS', {index: this.index, type: "payment", status: "resolve"});
				}).catch((error) => {
					// console.log(error);
				});
		},
		reject(){
			notify(this.$f7, "Вы отменили запрос", this.item.requesting);
			this.wsPayment.send(JSON.stringify({
				from: "user",
				status: "reject",
				hash: ["false"],
				_id: this.item._id,
			}));
			clearInterval(this.intervalID);
			this.$store.commit('CHANGE_STATUS', {index: this.index, type: "payment", status: "reject"});
		},
	},

	computed: {
		...mapState({
			address: state => state.wallet.address,
			private: state => state.wallet.private,
			minterGate: state => state.wallet.minterGate,
			wsPayment:  state => state.payments.wsPayment,
		}),
	},
	created() {
		this.countdown();
	},
	mounted() {
		// console.log(this.item)
	}
};
</script>
