<template>
	<f7-app :params="f7params">
		<f7-panel right resizable>
			<f7-view right>
				<f7-page>
					<f7-block>
						<p>Beta версия кошелька</p>
						Проект Cloud Projects
					</f7-block>
					<f7-list>
						<f7-list-item link="/balance" view="#main__view" panel-close title="Баланс"></f7-list-item>
						<f7-list-item link="/send" view="#main__view" panel-close title="Отправка"></f7-list-item>
						<f7-list-item link="/me" view="#main__view" panel-close title="Мой адрес"></f7-list-item>
						<f7-list-item link="/delegations" view="#main__view" panel-close title="Делегированно"></f7-list-item>
						<f7-list-item link="/history" view="#main__view" panel-close title="История"></f7-list-item>
						<f7-list-item divider title=""></f7-list-item>
						<f7-list-item link="/auth" title="Авторизации"></f7-list-item>
						<f7-list-item link="/settings" title="Настройки"></f7-list-item>
						<f7-list-item link="/about" title="О проекте"></f7-list-item>
						<f7-list-item link="#" @click="$store.commit('LOGOUT')" panel-close title="Выйти"></f7-list-item>
					</f7-list>
				</f7-page>
			</f7-view>
		</f7-panel>
		
		<f7-view id="main__view" :push-state="true" main url="/"></f7-view>
	</f7-app>
</template>

<script>
	import routes from './routes.js';
	import { Minter, API_TYPE_NODE } from "minter-js-sdk";
	import {mapGetters, mapState} from 'vuex';

	export default {
		data() {
			return {
				f7params: {
					routes,
					name: 'CP002',
					id: 'com.myapp.test',
				}
			}
		},
		computed: {
			...mapGetters([
				'isLogin',
			]),
		},
		mounted() {
		},
		created(){
			if (localStorage.getItem('wallet')) {
				try {
					this.$store.dispatch('SET_WALLET', JSON.parse(localStorage.getItem('wallet')));
				} catch(e) {
					localStorage.removeItem('wallet');
				}
			}
			if(this.isLogin){
				this.$store.dispatch('SET_GATE');
				this.$store.dispatch('FETCH_BALANCE');
				this.$store.dispatch('FETCH_DELEGATION');
			}
		}
	}
</script>