import { atobUTF16 } from '../../utils';

const state = {
	wsPayment: null,
	// api: "wss://cp002.na4u.ru/api/",
	api: "ws://localhost:3000/api/",
	payment: [],
	bill: [],
	auth: [],
};

const getters = {
	paymentList: state => {
		let list = [];
	}
	// isLogin: state => (!!state.address && !!state.address.length),
};

const mutations = {
	SET_WSPAYMENT: (state, payload) => {
		state.wsPayment = payload;
	},
	PUSH_PAYMENT: (state, payload) => {
		state.payment.push(payload);
	},
	CHANGE_STATUS: (state, payload) => {
		state[payload.type][payload.index].status = payload.status;
		// state.payment[payload.index].status = payload.status;
	},
};

const actions = {
	WS_PAYMENT: async (context, payload) => {
		let ws = new WebSocket(`${state.api}payment`);
		context.commit('SET_WSPAYMENT', ws);
	},
	PUSH_PAYMENT: async (context, payload) => {
		context.commit('PUSH_PAYMENT', payload);
	},
	SET_PAYMENTS: async (context, payload) => {
		payload.requests.forEach((item)=>{
			let txArray = item.txArray.map((txParams)=>{
				return JSON.parse(atobUTF16(txParams));
			});
			item.txArray = txArray;
			item.date = payload.date;
			// item.status = false;
			console.log(item)
			context.commit('PUSH_PAYMENT', item);
		});
	},
};

export default {
	state,
	getters,
	mutations,
	actions,
};