<template>
	<f7-page>
		<f7-navbar title="Это будет наш секрет" back-link="Назад"></f7-navbar>
		<!-- <f7-login-screen-title>It will be our little secret</f7-login-screen-title> -->
		<!-- label="Enter your seed phrase" -->
		<f7-list form no-hairlines-md>
			<f7-list-input
				outline
				floating-label
				label="Вставьте свою сид фразу"
				type="textarea"
				resizable
				placeholder=""
				clear-button
				:value="mnemonic" 
				@input="mnemonic = $event.target.value"
			>
			</f7-list-input>
		</f7-list>
		<f7-block-footer>
			<f7-button fill raised large title="sign in" @click.prevent="signIn">Войти</f7-button>
		</f7-block-footer>
		<f7-block v-if="seedError">
			<p>Сид недействителен</p>
		</f7-block>
	</f7-page>
</template>


<script>
import {isValidMnemonic, walletFromMnemonic} from 'minterjs-wallet';

export default {
	data() {
		return {
			mnemonic: "",
			seedError: false
		}
	},
	methods: {
		signIn(){
			if(isValidMnemonic(this.mnemonic)) this.authSeed();
			else this.error();
		},
		error() {
			console.log("errorSeed");
			this.seedError = true;
		},
		authSeed() {
			let wallet = walletFromMnemonic(this.mnemonic);

			this.$store.dispatch('SET_WALLET', {
				address: wallet.getAddressString(),
				private: wallet.getPrivateKeyString(),
				public: wallet.getPublicKeyString(),
			});
			this.$f7router.navigate('/');
		}
	},
	
}
</script>
